import { PaymentComponentsMap } from '../models/components-map.model';
import { PayordTextboxComponent } from '../components/textbox/payord-text.component';
import { Injectable } from '@angular/core';
import { PaymentTypeConfig } from '@backbase/payment-orders-ang';

@Injectable()
export class ComponentsFactoryService {
  private componentsMap = { ...PaymentComponentsMap };

  /**
   * Registers custom fields
   *
   * @param components Payment components
   * @returns
   */
  registerCustomFields(components: typeof PaymentComponentsMap = {}) {
    for (const key in components) {
      if (components.hasOwnProperty(key)) {
        this.componentsMap[key] = components[key];
      }
    }
  }

  /**
   * Registers custom fields for payment configs
   *
   * @param paymentConfigs Payment configurations
   * @returns
   */
  registerCustomFieldsForPaymentConfigs(
    paymentConfigs: Array<PaymentTypeConfig>
  ) {
    for (const config of paymentConfigs) {
      if (config.customFields) {
        this.registerCustomFields(config.customFields);
      }
    }
  }

  /**
   * Gets a form field
   *
   * @param formFieldType Type of the form field
   * @returns
   */
  getFormField(formFieldType: string) {
    return this.componentsMap[formFieldType] || PayordTextboxComponent;
  }
}
